<template>
  <!-- 口碑列表 -->
  <div class="bangdan">
    <van-tabs v-model="active" swipeable :swipe-threshold="5" color="#5f93ff" animated>
      <van-tab :title="item.title" v-for="(item, index) in list" :key="index">
        <div
          class="mybd clearfix"
          v-for="(items,index) in item.array.Data"
          :key="items.SerialID"
          @click="toCarInfo(items.SerialID)"
        >
          <div class="index">
            <span>{{index+1}}</span>
          </div>
          <div class="img">
            <van-image :src="items.Picture" width="120" height="80">
              <template v-slot:loading>
                <van-loading type="spinner" size="20" />
              </template>
            </van-image>
          </div>
          <div class="car">
            <p class="carname">{{items.Name}}</p>
            <p class="price">{{items.DealerPrice}}</p>
          </div>
          <div class="country">
            <p>全国销量</p>
            <span>{{items.IndexStr}}台</span>
          </div>
        </div>
      </van-tab>
    </van-tabs>
  </div>
</template>
<script>
import * as api from "../../api/index.js";
export default {
  data() {
    return {
      active: 0,
      index: 1,
      list: [
        {
          title: "SUV",
          array: []
        },
        {
          title: "紧凑型",
          array: []
        },
        {
          title: "小型车",
          array: []
        },
        {
          title: "中型车",
          array: []
        },
        {
          title: "MPV",
          array: []
        }
      ]
    };
  },
  created() {
    this.getOpinions(this.index);
  },
  methods: {
    async getOpinions(index) {
      let { jcx, mpv, suv, xxc, zxc } = await api.getOpinions(index);
      let arr = [suv, jcx, xxc, zxc, mpv];
      this.list.forEach((item, i) => {
        item.array = arr[i];
      });
    },
    toCarInfo(id) {
      this.$router.push({ path: "/carinfo", query: { SerialID: id } });
    }
  }
};
</script>
