<template>
  <div class="Carlist">
    <!-- 标题 -->
    <van-nav-bar :title="title" :fixed="true" />

    <!-- 选项卡 -->
    <van-tabs
      v-model="activeName"
      background="#5f93ff"
      title-active-color="#5f93ff"
      line-width="0px"
      title-inactive-color="rgba(255,255,255,.5)"
    >
      <van-tab title="销量榜" name="销量"><sales-volume></sales-volume></van-tab>
      <van-tab title="人气榜" name="人气"><popularity></popularity></van-tab>
      <van-tab title="口碑榜" name="口碑"><opinions></opinions></van-tab>
    </van-tabs>

    <!-- 底部导航 -->
    <tabbar></tabbar>
  </div>
</template>
<script>
import tabbar from "../../components/tabBar"; //底部导航栏
import salesVolume from "./salesVolume"  //销量列表
import popularity from "./popularity"   //人气列表
import opinions from "./opinions"  //口碑列表
export default {
  data() {
    return {
      activeName: "销量"
    };
  },
  computed: {
    title() {
      let d = new Date();
      let month = d.getMonth() + 1;
      return month + "月" + this.activeName + "排行";
    }
  },
  components: {
    tabbar,salesVolume,popularity,opinions
  }
};
</script>
<style lang="scss">
.Carlist {
  padding: 50px 0 50px;
  .van-nav-bar {
    z-index: 0 !important;
    height: 50px;
    background: #5f93ff;
    .van-nav-bar__title {
      color: #fff;
    }
  }
  .van-tabs {
    top: -2px;
    padding-top: 15px;
    background: #5f93ff;
    .van-tab {
      border-radius: 50px;
      height: 35px;
      line-height: 35px;
      width: 100px;
      margin: 0 10px;
    }
    .van-tabs__content,
    .van-tab--active {
      background: #fff;
    }
  }
}
</style>
